import React from 'react'
import Helmet from 'react-helmet'
import { withPrefix } from 'gatsby-link'
import AppIcons from '../partials/AppIcons'
import FooterInfo from '../partials/FooterInfo'

const DownloadPage = () => (
  <div>
    <Helmet
      title="Fitato App For Android & IOs | Download For Free"
    >
      <meta name="description" content="Download the Fitato App to buy a single pass to access all the fitness centers." />
    </Helmet>
    <div className="download-page align-center">
      <div className="container">
        <h1>Download the Fitato App</h1>
        <p>Everything you need to access 300+ gyms and studios.</p>
        <AppIcons />
        <img src={withPrefix('/static/fitato-app.png')} className="download-page__preview" alt="fitato app preview"/>
      </div>
    </div>
    <FooterInfo />
  </div>
)

export default DownloadPage



